/**
 * GitHub API  
 * Octokit    /
 */

import { Octokit } from '@octokit/rest';

const REPO_OWNER = 'jiunbae';
const REPO_NAME = 'jiunbae.github.io';
const BRANCH = 'main';

export interface GitHubFile {
  path: string;
  content: string;
  sha?: string;
}

export interface Post {
  title: string;
  description: string;
  date: string;
  slug: string;
  tags: string[];
  heroImage?: string;
  heroImageAlt?: string;
  published?: boolean;
  content: string;
  path: string;
  sha?: string;
}

export interface Review {
  title: string;
  mediaType: 'movie' | 'animation' | 'tv' | 'book' | 'game';
  rating: number;
  oneLiner: string;
  slug: string;
  date: string;
  tags: string[];
  poster?: string;
  metadata: {
    originalTitle?: string;
    year?: number;
    director?: string;
    creator?: string;
    author?: string;
    genre?: string[];
    runtime?: string;
    pages?: number;
  };
  externalIds?: {
    tmdbId?: string;
    imdbId?: string;
  };
  published?: boolean;
  content: string;
  path: string;
  sha?: string;
}

/**
 * Octokit  
 */
export const createOctokit = (token: string): Octokit => {
  return new Octokit({ auth: token });
};

/**
 * GitHub   
 */
export const validateToken = async (token: string): Promise<boolean> => {
  try {
    const octokit = createOctokit(token);
    await octokit.users.getAuthenticated();
    return true;
  } catch (error) {
    console.error('Token validation failed:', error);
    return false;
  }
};

/**
 *   
 */
export const getFileContent = async (
  octokit: Octokit,
  path: string
): Promise<GitHubFile | null> => {
  try {
    const { data } = await octokit.repos.getContent({
      owner: REPO_OWNER,
      repo: REPO_NAME,
      path,
      ref: BRANCH,
    });

    if ('content' in data && data.type === 'file') {
      // Browser-compatible base64 decoding
      const base64Content = data.content.replace(/\n/g, '');
      const decodedContent = atob(base64Content);
      // Convert to UTF-8
      const content = decodeURIComponent(escape(decodedContent));
      return {
        path,
        content,
        sha: data.sha,
      };
    }

    return null;
  } catch (error) {
    console.error(`Failed to get file content: ${path}`, error);
    return null;
  }
};

/**
 *   
 */
export const getDirectoryContents = async (
  octokit: Octokit,
  path: string
): Promise<Array<{ name: string; path: string; type: string }>> => {
  try {
    const { data } = await octokit.repos.getContent({
      owner: REPO_OWNER,
      repo: REPO_NAME,
      path,
      ref: BRANCH,
    });

    if (Array.isArray(data)) {
      return data.map((item) => ({
        name: item.name,
        path: item.path,
        type: item.type,
      }));
    }

    return [];
  } catch (error) {
    console.error(`Failed to get directory contents: ${path}`, error);
    return [];
  }
};

/**
 *   
 */
export const getPosts = async (
  octokit: Octokit,
  type: 'posts' | 'notes' = 'posts'
): Promise<Post[]> => {
  try {
    const basePath = `contents/${type}`;
    const items = await getDirectoryContents(octokit, basePath);

    const posts: Post[] = [];

    for (const item of items) {
      if (type === 'posts' && item.type === 'dir') {
        // posts  : contents/posts/YYYY-MM-DD-slug/index.md
        const indexFile = await getFileContent(octokit, `${item.path}/index.md`);
        if (indexFile) {
          const parsed = parseMarkdownFile(indexFile.content);
          posts.push({
            ...parsed.frontmatter,
            content: parsed.content,
            path: `${item.path}/index.md`,
            sha: indexFile.sha,
          });
        }
      } else if (type === 'notes' && item.type === 'file' && item.name.endsWith('.md')) {
        // notes  : contents/notes/YYYY-MM-DD-slug.md
        const file = await getFileContent(octokit, item.path);
        if (file) {
          const parsed = parseMarkdownFile(file.content);
          posts.push({
            ...parsed.frontmatter,
            content: parsed.content,
            path: item.path,
            sha: file.sha,
          });
        }
      }
    }

    return posts.sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
  } catch (error) {
    console.error('Failed to get posts:', error);
    return [];
  }
};

/**
 *    (frontmatter + content)
 */
export const parseMarkdownFile = (
  content: string
): {
  frontmatter: Omit<Post, 'content' | 'path' | 'sha'>;
  content: string;
} => {
  const frontmatterRegex = /^---\n([\s\S]*?)\n---\n([\s\S]*)$/;
  const match = content.match(frontmatterRegex);

  if (!match) {
    return {
      frontmatter: {
        title: '',
        description: '',
        date: new Date().toISOString().split('T')[0],
        slug: '',
        tags: [],
      },
      content: content,
    };
  }

  const frontmatterText = match[1];
  const bodyContent = match[2];

  const frontmatter: Omit<Post, 'content' | 'path' | 'sha'> = {
    title: '',
    description: '',
    date: new Date().toISOString().split('T')[0],
    slug: '',
    tags: [],
    published: true,
  };

  frontmatterText.split('\n').forEach((line) => {
    const [key, ...valueParts] = line.split(':');
    if (key && valueParts.length > 0) {
      const value = valueParts.join(':').trim();
      const trimmedKey = key.trim() as keyof typeof frontmatter;

      if (trimmedKey === 'tags') {
        // Parse array: [tag1, tag2]
        const arrayMatch = value.match(/\[(.*?)\]/);
        if (arrayMatch) {
          frontmatter.tags = arrayMatch[1]
            .split(',')
            .map((t) => t.trim().replace(/['"]/g, ''))
            .filter(Boolean);
        }
      } else if (trimmedKey === 'published') {
        // Parse boolean
        frontmatter.published = value === 'true' || value === 'True';
      } else if (trimmedKey === 'heroImage' || trimmedKey === 'heroImageAlt') {
        // Handle optional fields - set undefined if empty
        if (value && value !== '') {
          frontmatter[trimmedKey] = value;
        } else {
          frontmatter[trimmedKey] = undefined;
        }
      } else if (trimmedKey in frontmatter) {
        // Only set known properties
        (frontmatter as Record<string, string | string[] | undefined | boolean>)[trimmedKey] = value;
      }
    }
  });

  return {
    frontmatter,
    content: bodyContent.trim(),
  };
};

/**
 *    (frontmatter + content)
 */
export const createMarkdownFile = (
  frontmatter: Omit<Post, 'content' | 'path' | 'sha'>,
  content: string
): string => {
  // published    true 
  const published = frontmatter.published !== undefined ? frontmatter.published : true;

  const frontmatterText = `---
title: ${frontmatter.title}
description: ${frontmatter.description}
date: ${frontmatter.date}
slug: ${frontmatter.slug}
tags: [${frontmatter.tags.join(', ')}]${
    frontmatter.heroImage
      ? `
heroImage: ${frontmatter.heroImage}`
      : ''
  }${
    frontmatter.heroImageAlt
      ? `
heroImageAlt: ${frontmatter.heroImageAlt}`
      : ''
  }
published: ${published}
---

${content}`;

  return frontmatterText;
};

/**
 *    
 */
export const createOrUpdateFile = async (
  octokit: Octokit,
  path: string,
  content: string,
  message: string,
  sha?: string
): Promise<boolean> => {
  try {
    // Browser-compatible base64 encoding
    const contentBase64 = btoa(unescape(encodeURIComponent(content)));

    await octokit.repos.createOrUpdateFileContents({
      owner: REPO_OWNER,
      repo: REPO_NAME,
      path,
      message,
      content: contentBase64,
      branch: BRANCH,
      sha,
    });

    return true;
  } catch (error) {
    console.error('Failed to create or update file:', error);
    return false;
  }
};

/**
 *  
 */
export const uploadImage = async (
  octokit: Octokit,
  path: string,
  imageData: string | ArrayBuffer,
  message: string = 'Upload image'
): Promise<string | null> => {
  try {
    let contentBase64: string;

    if (typeof imageData === 'string') {
      // Base64 string
      contentBase64 = imageData.replace(/^data:image\/\w+;base64,/, '');
    } else {
      // ArrayBuffer - browser-compatible conversion
      const bytes = new Uint8Array(imageData);
      let binary = '';
      for (let i = 0; i < bytes.byteLength; i++) {
        binary += String.fromCharCode(bytes[i]);
      }
      contentBase64 = btoa(binary);
    }

    await octokit.repos.createOrUpdateFileContents({
      owner: REPO_OWNER,
      repo: REPO_NAME,
      path,
      message,
      content: contentBase64,
      branch: BRANCH,
    });

    // Return the relative path for markdown
    return `/${path}`;
  } catch (error) {
    console.error('Failed to upload image:', error);
    return null;
  }
};

/**
 *  
 */
export const deleteFile = async (
  octokit: Octokit,
  path: string,
  sha: string,
  message: string
): Promise<boolean> => {
  try {
    await octokit.repos.deleteFile({
      owner: REPO_OWNER,
      repo: REPO_NAME,
      path,
      message,
      sha,
      branch: BRANCH,
    });

    return true;
  } catch (error) {
    console.error('Failed to delete file:', error);
    return false;
  }
};

/**
 * Review   
 */
export const parseReviewMarkdownFile = (
  content: string
): {
  frontmatter: Omit<Review, 'content' | 'path' | 'sha'>;
  content: string;
} => {
  const frontmatterRegex = /^---\n([\s\S]*?)\n---\n([\s\S]*)$/;
  const match = content.match(frontmatterRegex);

  if (!match) {
    return {
      frontmatter: {
        title: '',
        mediaType: 'movie',
        rating: 0,
        oneLiner: '',
        slug: '',
        date: new Date().toISOString().split('T')[0],
        tags: [],
        metadata: {},
        published: true,
      },
      content: content,
    };
  }

  const frontmatterText = match[1];
  const bodyContent = match[2];

  const frontmatter: Omit<Review, 'content' | 'path' | 'sha'> = {
    title: '',
    mediaType: 'movie',
    rating: 0,
    oneLiner: '',
    slug: '',
    date: new Date().toISOString().split('T')[0],
    tags: [],
    metadata: {},
    published: true,
  };

  let currentMetadataKey: string | null = null;
  let currentExternalIdsKey: string | null = null;

  frontmatterText.split('\n').forEach((line) => {
    // Handle nested objects (metadata, externalIds)
    if (line.trim() === 'metadata:') {
      currentMetadataKey = 'metadata';
      currentExternalIdsKey = null;
      return;
    }
    if (line.trim() === 'externalIds:') {
      currentExternalIdsKey = 'externalIds';
      currentMetadataKey = null;
      frontmatter.externalIds = {};
      return;
    }

    // Handle nested properties
    if (currentMetadataKey && line.startsWith('  ')) {
      const [key, ...valueParts] = line.trim().split(':');
      if (key && valueParts.length > 0) {
        const value = valueParts.join(':').trim();
        if (key === 'genre') {
          const arrayMatch = value.match(/\[(.*?)\]/) || value.match(/^-\s*(.+)/);
          if (arrayMatch) {
            frontmatter.metadata.genre = arrayMatch[1]
              .split(',')
              .map((t) => t.trim().replace(/['"]/g, ''))
              .filter(Boolean);
          }
        } else if (key === 'year' || key === 'pages') {
          frontmatter.metadata[key] = parseInt(value, 10);
        } else {
          (frontmatter.metadata as any)[key] = value;
        }
      }
      return;
    }

    if (currentExternalIdsKey && line.startsWith('  ') && frontmatter.externalIds) {
      const [key, ...valueParts] = line.trim().split(':');
      if (key && valueParts.length > 0) {
        const value = valueParts.join(':').trim().replace(/['"]/g, '');
        (frontmatter.externalIds as any)[key] = value;
      }
      return;
    }

    // Handle top-level properties
    const [key, ...valueParts] = line.split(':');
    if (key && valueParts.length > 0) {
      const value = valueParts.join(':').trim();
      const trimmedKey = key.trim();

      // Reset nested parsing when we hit a top-level key
      if (!line.startsWith('  ')) {
        currentMetadataKey = null;
        currentExternalIdsKey = null;
      }

      if (trimmedKey === 'tags') {
        const arrayMatch = value.match(/\[(.*?)\]/);
        if (arrayMatch) {
          frontmatter.tags = arrayMatch[1]
            .split(',')
            .map((t) => t.trim().replace(/['"]/g, ''))
            .filter(Boolean);
        }
      } else if (trimmedKey === 'rating') {
        frontmatter.rating = parseFloat(value);
      } else if (trimmedKey === 'mediaType') {
        frontmatter.mediaType = value as Review['mediaType'];
      } else if (trimmedKey === 'published') {
        frontmatter.published = value === 'true' || value === 'True';
      } else if (trimmedKey === 'poster') {
        if (value && value !== '') {
          frontmatter.poster = value;
        }
      } else if (
        trimmedKey === 'title' ||
        trimmedKey === 'oneLiner' ||
        trimmedKey === 'slug' ||
        trimmedKey === 'date'
      ) {
        (frontmatter as any)[trimmedKey] = value;
      }
    }
  });

  return {
    frontmatter,
    content: bodyContent.trim(),
  };
};

/**
 * Review   
 */
export const createReviewMarkdownFile = (
  frontmatter: Omit<Review, 'content' | 'path' | 'sha'>,
  content: string
): string => {
  // published    true 
  const published = frontmatter.published !== undefined ? frontmatter.published : true;

  const metadataLines: string[] = [];
  if (frontmatter.metadata.originalTitle)
    metadataLines.push(`  originalTitle: ${frontmatter.metadata.originalTitle}`);
  if (frontmatter.metadata.year) metadataLines.push(`  year: ${frontmatter.metadata.year}`);
  if (frontmatter.metadata.director)
    metadataLines.push(`  director: ${frontmatter.metadata.director}`);
  if (frontmatter.metadata.creator) metadataLines.push(`  creator: ${frontmatter.metadata.creator}`);
  if (frontmatter.metadata.author) metadataLines.push(`  author: ${frontmatter.metadata.author}`);
  if (frontmatter.metadata.genre && frontmatter.metadata.genre.length > 0) {
    metadataLines.push(`  genre:`);
    frontmatter.metadata.genre.forEach((g) => metadataLines.push(`    - ${g}`));
  }
  if (frontmatter.metadata.runtime) metadataLines.push(`  runtime: ${frontmatter.metadata.runtime}`);
  if (frontmatter.metadata.pages) metadataLines.push(`  pages: ${frontmatter.metadata.pages}`);

  const externalIdsLines: string[] = [];
  if (frontmatter.externalIds?.tmdbId)
    externalIdsLines.push(`  tmdbId: '${frontmatter.externalIds.tmdbId}'`);
  if (frontmatter.externalIds?.imdbId)
    externalIdsLines.push(`  imdbId: '${frontmatter.externalIds.imdbId}'`);

  const frontmatterText = `---
title: ${frontmatter.title}
mediaType: ${frontmatter.mediaType}
rating: ${frontmatter.rating}
oneLiner: ${frontmatter.oneLiner}
slug: ${frontmatter.slug}
date: '${frontmatter.date}'
tags:
${frontmatter.tags.map((tag) => `  - ${tag}`).join('\n')}${
    frontmatter.poster
      ? `
poster: ${frontmatter.poster}`
      : ''
  }${
    metadataLines.length > 0
      ? `
metadata:
${metadataLines.join('\n')}`
      : ''
  }${
    externalIdsLines.length > 0
      ? `
externalIds:
${externalIdsLines.join('\n')}`
      : ''
  }
published: ${published}
---

${content}`;

  return frontmatterText;
};

/**
 * Review  
 */
export const getReviews = async (octokit: Octokit): Promise<Review[]> => {
  try {
    const basePath = 'contents/reviews';
    const items = await getDirectoryContents(octokit, basePath);

    const reviews: Review[] = [];

    for (const item of items) {
      if (item.type === 'dir') {
        // reviews  : contents/reviews/YYYY-MM-DD-slug/index.md
        const indexFile = await getFileContent(octokit, `${item.path}/index.md`);
        if (indexFile) {
          const parsed = parseReviewMarkdownFile(indexFile.content);
          reviews.push({
            ...parsed.frontmatter,
            content: parsed.content,
            path: `${item.path}/index.md`,
            sha: indexFile.sha,
          });
        }
      }
    }

    return reviews.sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
  } catch (error) {
    console.error('Failed to get reviews:', error);
    return [];
  }
};
