/**
 * GitHub API   Context
 */

import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { Octokit } from '@octokit/rest';
import { getGitHubToken, saveGitHubToken, removeGitHubToken } from '@/utils/storage';
import { createOctokit, validateToken } from '@/utils/github';

interface GitHubContextType {
  octokit: Octokit | null;
  token: string | null;
  isAuthenticated: boolean;
  isValidating: boolean;
  error: string | null;
  login: (token: string) => Promise<boolean>;
  logout: () => void;
}

const GitHubContext = createContext<GitHubContextType | undefined>(undefined);

export const GitHubProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [octokit, setOctokit] = useState<Octokit | null>(null);
  const [token, setToken] = useState<string | null>(null);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isValidating, setIsValidating] = useState(true);
  const [error, setError] = useState<string | null>(null);

  //     
  useEffect(() => {
    const initAuth = async () => {
      try {
        const savedToken = await getGitHubToken();
        if (savedToken) {
          const isValid = await validateToken(savedToken);
          if (isValid) {
            setToken(savedToken);
            setOctokit(createOctokit(savedToken));
            setIsAuthenticated(true);
            setError(null);
          } else {
            removeGitHubToken();
            setError('    .  .');
          }
        }
      } catch (error) {
        console.error('Failed to load token:', error);
        setError('  .');
        removeGitHubToken();
      } finally {
        setIsValidating(false);
      }
    };

    initAuth();
  }, []);

  const login = async (newToken: string): Promise<boolean> => {
    setIsValidating(true);
    try {
      const isValid = await validateToken(newToken);

      if (isValid) {
        await saveGitHubToken(newToken);
        setToken(newToken);
        setOctokit(createOctokit(newToken));
        setIsAuthenticated(true);
        setError(null);
        return true;
      }

      setError('  .');
      return false;
    } catch (error) {
      console.error('Login failed:', error);
      setError(' .');
      return false;
    } finally {
      setIsValidating(false);
    }
  };

  const logout = () => {
    removeGitHubToken();
    setToken(null);
    setOctokit(null);
    setIsAuthenticated(false);
    setError(null);
  };

  return (
    <GitHubContext.Provider
      value={{
        octokit,
        token,
        isAuthenticated,
        isValidating,
        error,
        login,
        logout,
      }}
    >
      {children}
    </GitHubContext.Provider>
  );
};

export const useGitHub = (): GitHubContextType => {
  const context = useContext(GitHubContext);
  if (!context) {
    throw new Error('useGitHub must be used within a GitHubProvider');
  }
  return context;
};
