/**
 * Draft  
 *      /
 */

import React, { useState, useEffect } from 'react';
import { getDrafts, deleteDraft, deleteAllDrafts, Draft } from '@/utils/storage';
import { format } from 'date-fns';

interface DraftManagerProps {
  onLoadDraft: (draft: Draft) => void;
  currentDraftId?: string;
}

const DraftManager: React.FC<DraftManagerProps> = ({ onLoadDraft, currentDraftId }) => {
  const [drafts, setDrafts] = useState<Draft[]>([]);
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    loadDrafts();
  }, []);

  const loadDrafts = () => {
    setDrafts(getDrafts());
  };

  const handleDeleteDraft = (id: string) => {
    if (confirm('   ?')) {
      deleteDraft(id);
      loadDrafts();
    }
  };

  const handleDeleteAllDrafts = () => {
    if (confirm(`   (${drafts.length}) ?`)) {
      deleteAllDrafts();
      loadDrafts();
      setIsOpen(false);
    }
  };

  const handleLoadDraft = (draft: Draft) => {
    if (
      currentDraftId !== draft.id &&
      confirm('      ?')
    ) {
      onLoadDraft(draft);
      setIsOpen(false);
    } else if (currentDraftId === draft.id) {
      onLoadDraft(draft);
      setIsOpen(false);
    }
  };

  if (drafts.length === 0) {
    return null;
  }

  return (
    <div className="draft-manager">
      <button onClick={() => setIsOpen(!isOpen)} className="btn-drafts">
          ({drafts.length})
      </button>

      {isOpen && (
        <div className="draft-modal">
          <div className="draft-modal-content">
            <div className="draft-modal-header">
              <h3>  </h3>
              <div style={{ display: 'flex', gap: '0.5rem', alignItems: 'center' }}>
                <button onClick={handleDeleteAllDrafts} className="btn-delete-all">
                   
                </button>
                <button onClick={() => setIsOpen(false)} className="btn-close">
                  
                </button>
              </div>
            </div>

            <ul className="draft-list">
              {drafts.map((draft) => (
                <li key={draft.id} className="draft-item">
                  <div className="draft-item-content">
                    <h4>{draft.title || '( )'}</h4>
                    <p className="draft-item-meta">
                      {format(new Date(draft.savedAt), 'yyyy-MM-dd HH:mm:ss')}
                      {'  '}
                      {draft.type === 'post' ? '' : ''}
                    </p>
                  </div>
                  <div className="draft-item-actions">
                    <button
                      onClick={() => handleLoadDraft(draft)}
                      className="btn-load"
                    >
                      
                    </button>
                    <button
                      onClick={() => handleDeleteDraft(draft.id)}
                      className="btn-delete"
                    >
                      
                    </button>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>
      )}
    </div>
  );
};

export default DraftManager;
