/**
 * Frontmatter   
 * , , , ,  
 */

import React from 'react';
import { Post } from '@/utils/github';

interface FrontmatterFormProps {
  frontmatter: Omit<Post, 'content' | 'path' | 'sha'>;
  onChange: (frontmatter: Omit<Post, 'content' | 'path' | 'sha'>) => void;
  postType: 'post' | 'note';
}

const FrontmatterForm: React.FC<FrontmatterFormProps> = ({ frontmatter, onChange, postType }) => {
  const handleChange = (field: string, value: any) => {
    onChange({
      ...frontmatter,
      [field]: value,
    });
  };

  const handleTagsChange = (tagsString: string) => {
    const tags = tagsString
      .split(',')
      .map((t) => t.trim())
      .filter(Boolean);
    handleChange('tags', tags);
  };

  const generateSlug = () => {
    const slug = frontmatter.title
      .toLowerCase()
      .replace(/[^a-z0-9-\s-]/g, '')
      .replace(/\s+/g, '-')
      .replace(/-+/g, '-')
      .trim();

    // notes  /notes/ prefix 
    const finalSlug = postType === 'note' ? `/notes/${slug}` : `/${slug}`;
    handleChange('slug', finalSlug);
  };

  return (
    <div className="frontmatter-form">
      <div className="form-group">
        <label htmlFor="title"> *</label>
        <input
          id="title"
          type="text"
          value={frontmatter.title}
          onChange={(e) => handleChange('title', e.target.value)}
          placeholder=" "
          required
        />
      </div>

      <div className="form-group">
        <label htmlFor="description"></label>
        <textarea
          id="description"
          value={frontmatter.description}
          onChange={(e) => handleChange('description', e.target.value)}
          placeholder="   "
          rows={2}
        />
      </div>

      <div className="form-row">
        <div className="form-group">
          <label htmlFor="date"> *</label>
          <input
            id="date"
            type="date"
            value={frontmatter.date}
            onChange={(e) => handleChange('date', e.target.value)}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="slug"> *</label>
          <div className="slug-input-wrapper">
            <input
              id="slug"
              type="text"
              value={frontmatter.slug}
              onChange={(e) => handleChange('slug', e.target.value)}
              placeholder="/my-post"
              required
            />
            <button type="button" onClick={generateSlug} className="btn-generate-slug">
               
            </button>
          </div>
        </div>
      </div>

      <div className="form-group">
        <label htmlFor="tags"></label>
        <input
          id="tags"
          type="text"
          value={frontmatter.tags.join(', ')}
          onChange={(e) => handleTagsChange(e.target.value)}
          placeholder="react, typescript, web ( )"
        />
        <small>(,)  </small>
      </div>

      <div className="form-group">
        <label htmlFor="heroImage">  URL</label>
        <input
          id="heroImage"
          type="text"
          value={frontmatter.heroImage || ''}
          onChange={(e) => handleChange('heroImage', e.target.value)}
          placeholder="/images/hero.jpg"
        />
      </div>

      <div className="form-group">
        <label htmlFor="heroImageAlt">  </label>
        <input
          id="heroImageAlt"
          type="text"
          value={frontmatter.heroImageAlt || ''}
          onChange={(e) => handleChange('heroImageAlt', e.target.value)}
          placeholder="  ()"
        />
      </div>

      <div className="form-group">
        <label htmlFor="published">
          <input
            id="published"
            type="checkbox"
            checked={frontmatter.published !== false}
            onChange={(e) => handleChange('published', e.target.checked)}
            style={{ marginRight: '8px', width: 'auto' }}
          />
           
        </label>
        <small>    </small>
      </div>
    </div>
  );
};

export default FrontmatterForm;
