/**
 * GitHub  
 * Personal Access Token   
 */

import React, { useState } from 'react';
import { useGitHub } from '@/contexts/GitHubContext';

const Auth: React.FC = () => {
  const { login, logout, isAuthenticated, isValidating, error: contextError } = useGitHub();
  const [tokenInput, setTokenInput] = useState('');
  const [localError, setLocalError] = useState('');
  const [showToken, setShowToken] = useState(false);
  const [isLoggingIn, setIsLoggingIn] = useState(false);

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setLocalError('');

    if (!tokenInput.trim()) {
      setLocalError(' .');
      return;
    }

    setIsLoggingIn(true);
    const success = await login(tokenInput);
    setIsLoggingIn(false);

    if (!success) {
      setLocalError('  .  .');
    } else {
      setTokenInput('');
    }
  };

  const handleLogout = () => {
    if (confirm('?   .')) {
      logout();
    }
  };

  if (isValidating) {
    return (
      <div className="auth-container">
        <div className="auth-loading">
          <p>  ...</p>
        </div>
      </div>
    );
  }

  if (isAuthenticated) {
    return (
      <div className="auth-status">
        <span className="auth-status-badge"> </span>
        <button onClick={handleLogout} className="btn-logout">
          
        </button>
      </div>
    );
  }

  const displayError = contextError || localError;

  return (
    <div className="auth-container">
      <div className="auth-card">
        <h2>GitHub </h2>
        <p className="auth-description">
            GitHub Personal Access Token .
        </p>

        {displayError && (
          <div className="auth-error">
            <p>{displayError}</p>
          </div>
        )}

        <form onSubmit={handleLogin}>
          <div className="form-group">
            <label htmlFor="token">Personal Access Token</label>
            <div className="token-input-wrapper">
              <input
                id="token"
                type={showToken ? 'text' : 'password'}
                value={tokenInput}
                onChange={(e) => setTokenInput(e.target.value)}
                placeholder="ghp_xxxxxxxxxxxxxxxxxxxx"
                className="token-input"
                autoComplete="off"
              />
              <button
                type="button"
                onClick={() => setShowToken(!showToken)}
                className="btn-toggle-visibility"
              >
                {showToken ? '' : ''}
              </button>
            </div>
          </div>

          <button type="submit" className="btn-login" disabled={isLoggingIn}>
            {isLoggingIn ? ' ...' : ''}
          </button>
        </form>

        <div className="auth-help">
          <h3>  </h3>
          <ol>
            <li>
              <a
                href="https://github.com/settings/tokens/new"
                target="_blank"
                rel="noopener noreferrer"
              >
                GitHub Settings  Developer settings  Personal access tokens
              </a>
            </li>
            <li>
              "Generate new token (classic)" 
            </li>
            <li>
               : <code>repo</code> 
            </li>
            <li>
                   
            </li>
          </ol>
          <p className="auth-warning">
              :   .    .
          </p>
        </div>
      </div>
    </div>
  );
};

export default Auth;
