/**
 *   
 * Frontmatter, ,  , Draft  
 */

import React, { useState, useEffect } from 'react';
import MDEditor from '@uiw/react-md-editor';
import { useGitHub } from '@/contexts/GitHubContext';
import {
  Post,
  Review,
  createOrUpdateFile,
  createMarkdownFile,
  createReviewMarkdownFile,
} from '@/utils/github';
import { saveDraft, Draft } from '@/utils/storage';
import FrontmatterForm from './FrontmatterForm';
import ReviewForm from './ReviewForm';
import ImageUploader from './ImageUploader';
import DraftManager from './DraftManager';
import { format } from 'date-fns';

interface EditorProps {
  post: Post | Review | null;
  postType: 'post' | 'note' | 'review';
  loadedDraft?: Draft | null;
  onSaved: () => void;
  onCancel: () => void;
}

const Editor: React.FC<EditorProps> = ({ post, postType, loadedDraft, onSaved, onCancel }) => {
  const { octokit } = useGitHub();
  const [content, setContent] = useState('');
  const [frontmatter, setFrontmatter] = useState<
    Omit<Post, 'content' | 'path' | 'sha'> | Omit<Review, 'content' | 'path' | 'sha'>
  >(
    postType === 'review'
      ? {
          title: '',
          mediaType: 'movie' as const,
          rating: 0,
          oneLiner: '',
          slug: '',
          date: format(new Date(), 'yyyy-MM-dd'),
          tags: [],
          metadata: {},
          published: true,
        }
      : {
          title: '',
          description: '',
          date: format(new Date(), 'yyyy-MM-dd'),
          slug: '',
          tags: [],
          heroImage: '',
          heroImageAlt: '',
          published: true,
        }
  );
  const [draftId] = useState(`draft_${Date.now()}`);
  const [saving, setSaving] = useState(false);
  const [saveStatus, setSaveStatus] = useState('');
  const [showImageUploader, setShowImageUploader] = useState(false);
  const [viewMode, setViewMode] = useState<'edit' | 'preview' | 'split'>('split');

  //    
  useEffect(() => {
    if (post) {
      setContent(post.content);
      if (postType === 'review') {
        const reviewPost = post as Review;
        setFrontmatter({
          title: reviewPost.title,
          mediaType: reviewPost.mediaType,
          rating: reviewPost.rating,
          oneLiner: reviewPost.oneLiner,
          slug: reviewPost.slug,
          date: reviewPost.date,
          tags: reviewPost.tags,
          poster: reviewPost.poster,
          metadata: reviewPost.metadata,
          externalIds: reviewPost.externalIds,
          published: reviewPost.published,
        });
      } else {
        const postData = post as Post;
        setFrontmatter({
          title: postData.title,
          description: postData.description,
          date: postData.date,
          slug: postData.slug,
          tags: postData.tags,
          heroImage: postData.heroImage,
          heroImageAlt: postData.heroImageAlt,
          published: postData.published,
        });
      }
    } else if (loadedDraft) {
      // Draft 
      setContent(loadedDraft.content);
      setFrontmatter(loadedDraft.frontmatter as any);
    } else {
      //  
      setContent('');
      if (postType === 'review') {
        setFrontmatter({
          title: '',
          mediaType: 'movie' as const,
          rating: 0,
          oneLiner: '',
          slug: '',
          date: format(new Date(), 'yyyy-MM-dd'),
          tags: [],
          metadata: {},
          published: true,
        });
      } else {
        setFrontmatter({
          title: '',
          description: '',
          date: format(new Date(), 'yyyy-MM-dd'),
          slug: '',
          tags: [],
          heroImage: '',
          heroImageAlt: '',
          published: true,
        });
      }
    }
  }, [post, postType, loadedDraft]);

  // Draft   (debounce: 5    )
  useEffect(() => {
    //    
    if (!content && !frontmatter.title) {
      return;
    }

    // 5    
    const timer = setTimeout(() => {
      const draft: Draft = {
        id: draftId,
        title: frontmatter.title || '( )',
        content,
        frontmatter: frontmatter as any,
        type: postType === 'review' ? 'post' : postType,
        savedAt: new Date().toISOString(),
      };
      saveDraft(draft);
      setSaveStatus(' ');
      setTimeout(() => setSaveStatus(''), 2000);
    }, 5000);

    // content frontmatter      
    return () => clearTimeout(timer);
  }, [content, frontmatter, draftId, postType]);

  //   
  const handleCancel = () => {
    if (!confirm(' ? (     )')) {
      return;
    }

    //   
    if (content || frontmatter.title) {
      const draft: Draft = {
        id: draftId,
        title: frontmatter.title || '( )',
        content,
        frontmatter: frontmatter as any,
        type: postType === 'review' ? 'post' : postType,
        savedAt: new Date().toISOString(),
      };
      saveDraft(draft);
    }
    onCancel();
  };

  //   
  const handleSave = async () => {
    if (!octokit) return;

    //  
    if (!frontmatter.title.trim()) {
      alert(' .');
      return;
    }

    if (!frontmatter.slug.trim()) {
      alert(' .');
      return;
    }

    if (!content.trim()) {
      alert(' .');
      return;
    }

    setSaving(true);

    try {
      //   
      const markdownContent =
        postType === 'review'
          ? createReviewMarkdownFile(frontmatter as Omit<Review, 'content' | 'path' | 'sha'>, content)
          : createMarkdownFile(frontmatter as Omit<Post, 'content' | 'path' | 'sha'>, content);

      //   
      let filePath: string;
      if (post) {
        //   
        filePath = post.path;
      } else {
        //  
        const datePrefix = frontmatter.date;
        let slugName = frontmatter.slug.replace(/^\//, '');

        if (postType === 'post') {
          // posts: contents/posts/YYYY-MM-DD-slug/index.md
          filePath = `contents/posts/${datePrefix}-${slugName}/index.md`;
        } else if (postType === 'review') {
          // reviews: contents/reviews/YYYY-MM-DD-slug/index.md
          slugName = slugName.replace(/^reviews\//, '');
          filePath = `contents/reviews/${datePrefix}-${slugName}/index.md`;
        } else {
          // notes: contents/notes/YYYY-MM-DD-slug.md
          // slug /notes/xxx  notes/ prefix 
          slugName = slugName.replace(/^notes\//, '');
          filePath = `contents/notes/${datePrefix}-${slugName}.md`;
        }
      }

      //  
      const commitMessage = post
        ? `Update ${postType}: ${frontmatter.title}`
        : `Add ${postType}: ${frontmatter.title}`;

      // GitHub 
      const success = await createOrUpdateFile(
        octokit,
        filePath,
        markdownContent,
        commitMessage,
        post?.sha
      );

      if (success) {
        setSaveStatus(' ! GitHub Actions  .');
        setTimeout(() => {
          onSaved();
        }, 2000);
      } else {
        alert(' .  .');
      }
    } catch (error) {
      console.error('Failed to save post:', error);
      alert('   .');
    } finally {
      setSaving(false);
    }
  };

  //     
  const handleImageUploaded = (imageUrl: string) => {
    const imageMarkdown = `![](${imageUrl})`;
    setContent((prev) => prev + '\n\n' + imageMarkdown);
    setShowImageUploader(false);
  };

  //  :    viewMode  
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth < 768) {
        setViewMode('edit');
      }
    };

    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <div className="editor-container">
      <div className="editor-header">
        <h2>
          {post
            ? ' '
            : ` ${postType === 'post' ? '' : postType === 'note' ? '' : ''}`}
        </h2>
        <div className="editor-actions">
          {saveStatus && <span className="save-status">{saveStatus}</span>}
          <button onClick={handleCancel} className="btn-cancel">
            
          </button>
          <button onClick={handleSave} className="btn-save" disabled={saving}>
            {saving ? ' ...' : '  '}
          </button>
        </div>
      </div>

      <div className="editor-content">
        {/* Frontmatter  */}
        <div className="editor-section">
          <h3></h3>
          {postType === 'review' ? (
            <ReviewForm
              frontmatter={frontmatter as Omit<Review, 'content' | 'path' | 'sha'>}
              onChange={setFrontmatter}
            />
          ) : (
            <FrontmatterForm
              frontmatter={frontmatter as Omit<Post, 'content' | 'path' | 'sha'>}
              onChange={setFrontmatter}
              postType={postType}
            />
          )}
        </div>

        {/*   */}
        <div className="editor-section">
          <div className="section-header">
            <h3> </h3>
            <button
              onClick={() => setShowImageUploader(!showImageUploader)}
              className="btn-toggle"
            >
              {showImageUploader ? '' : ''}
            </button>
          </div>
          {showImageUploader && (
            <ImageUploader
              postSlug={
                frontmatter.slug.replace(/^\//, '').replace(/^notes\//, '') || `draft-${draftId}`
              }
              postType={postType}
              onImageUploaded={handleImageUploaded}
            />
          )}
        </div>

        {/*   */}
        <div className="editor-section editor-main">
          <div className="section-header">
            <h3></h3>
            {/* :   */}
            <div className="view-mode-toggle">
              <button
                onClick={() => setViewMode('edit')}
                className={viewMode === 'edit' ? 'active' : ''}
              >
                
              </button>
              <button
                onClick={() => setViewMode('preview')}
                className={viewMode === 'preview' ? 'active' : ''}
              >
                
              </button>
              <button
                onClick={() => setViewMode('split')}
                className={viewMode === 'split' ? 'active' : ''}
              >
                
              </button>
            </div>
          </div>

          <MDEditor
            value={content}
            onChange={(val) => setContent(val || '')}
            preview={viewMode === 'edit' ? 'edit' : viewMode === 'preview' ? 'preview' : 'live'}
            height={500}
            visibleDragbar={false}
          />
        </div>
      </div>
    </div>
  );
};

export default Editor;
