/**
 *   
 *     GitHub 
 */

import React, { useState, useCallback } from 'react';
import { useDropzone } from 'react-dropzone';
import { useGitHub } from '@/contexts/GitHubContext';
import { uploadImage } from '@/utils/github';

interface ImageUploaderProps {
  postSlug: string;
  postType: 'post' | 'note' | 'review';
  onImageUploaded: (imageUrl: string) => void;
}

const ImageUploader: React.FC<ImageUploaderProps> = ({
  postSlug,
  postType,
  onImageUploaded,
}) => {
  const { octokit } = useGitHub();
  const [uploading, setUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState('');

  const onDrop = useCallback(
    async (acceptedFiles: File[]) => {
      if (!octokit || acceptedFiles.length === 0) return;

      setUploading(true);

      for (const file of acceptedFiles) {
        try {
          setUploadProgress(` : ${file.name}`);

          //  base64 
          const reader = new FileReader();
          reader.onload = async (e) => {
            if (!e.target?.result) return;

            const base64Data = e.target.result as string;

            //        
            const timestamp = Date.now();
            const fileExtension = file.name.split('.').pop()?.toLowerCase() || 'jpg';
            //       (directory traversal )
            const safeExtension = fileExtension.replace(/[^a-z0-9]/g, '');
            const fileName = `image_${timestamp}.${safeExtension}`;

            //    (postSlug     )
            const slugForPath = postSlug && postSlug !== '' ? postSlug : 'common';
            const uploadPath =
              postType === 'post'
                ? `contents/posts/${slugForPath}/${fileName}`
                : `contents/images/${fileName}`;

            // GitHub 
            const imageUrl = await uploadImage(
              octokit,
              uploadPath,
              base64Data,
              `Upload image: ${fileName}`
            );

            if (imageUrl) {
              onImageUploaded(imageUrl);
              setUploadProgress(` : ${file.name}`);
              setTimeout(() => setUploadProgress(''), 2000);
            } else {
              setUploadProgress(` : ${file.name}`);
              setTimeout(() => setUploadProgress(''), 3000);
            }
          };

          reader.readAsDataURL(file);
        } catch (error) {
          console.error('Failed to upload image:', error);
          setUploadProgress(` : ${file.name}`);
          setTimeout(() => setUploadProgress(''), 3000);
        }
      }

      setUploading(false);
    },
    [octokit, postSlug, postType, onImageUploaded]
  );

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      'image/*': ['.png', '.jpg', '.jpeg', '.gif', '.webp', '.svg'],
    },
    multiple: true,
    disabled: uploading,
  });

  return (
    <div className="image-uploader">
      <div
        {...getRootProps()}
        className={`dropzone ${isDragActive ? 'active' : ''} ${uploading ? 'uploading' : ''}`}
      >
        <input {...getInputProps()} />
        {uploading ? (
          <div className="dropzone-uploading">
            <p>{uploadProgress}</p>
          </div>
        ) : isDragActive ? (
          <p>  ...</p>
        ) : (
          <div className="dropzone-content">
            <p>   </p>
            <small>PNG, JPG, GIF, WebP, SVG </small>
          </div>
        )}
      </div>
    </div>
  );
};

export default ImageUploader;
